img {
  max-width: 100%;
  height: auto;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}
body {
  min-width: 320px;
  margin: 0;
  padding: 0;
  background-color: #181824;
  font-family: "Roboto", "Arial", sans-serif;
  color: #333333;
  font-size: 18px;
  font-weight: 400;
  line-height: 30px;
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}
.container {
  position: absolute;
  left: 50%;
  margin-top: 100px;
  padding-bottom: 20px;
  transform: translateX(-50%);
  width: calc(100% - 40px);
  min-width: 240px;
  max-width: 660px;
  background-color: #ffffff;
  border-radius: 15px;
  box-shadow: 0 8px 60px -10px rgba(95, 67, 164, 0.74);
}
.title {
  margin: 0;
  padding: 85px 0 0 0;
  color: #7157bd;
  font-size: 22px;
  font-weight: 700;
}
.photo {
  position: absolute;
  top: -60px;
  left: 50%;
  width: 120px;
  height: 120px;
  border-radius: 50%;
  background-color: #324e63;
  box-shadow: 0 5px 50px 0 #6c44fc, 0 0 0 7px rgba(107, 74, 255, 0.5);
  transform: translateX(-50%);
  overflow: hidden;
}
@media (min-width: 1200px) {
  .photo {
    top: -75px;
    width: 150px;
    height: 150px;
  }
}
.photo__pic {
  display: block;
  margin: 0;
  padding: 0;
  width: 120px;
  height: 100%;
}
@media (min-width: 1200px) {
  .photo__pic {
    width: 150px;
  }
}
.page__header {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.location {
  display: inline-flex;
  margin: 10px 0 0 0;
  padding: 0;
}
.location span {
  margin-left: 5px;
}
.part {
  margin: 10px 20px 0 20px;
  padding: 10px;
  border-radius: 20px;
  overflow: hidden;
  height: 50px;
  transition: all 0.5s ease-in-out;
  color: transparent;
}
.content {
  display: flex;
  flex-direction: column;
  padding-bottom: 20px;
}
.part__title {
  margin: 0;
  padding: 0;
  font-size: inherit;
  font-weight: 700;
  text-align: center;
  color: #ffffff;
  cursor: pointer;
  border-radius: 16px;
}
.part__text {
  margin: 10px 0 0 0;
  padding: 0;
  font-size: 16px;
  line-height: 25px;
}
.about-me {
  background-image: linear-gradient(225deg, #926fc8, #0396b2, #235761);
  box-shadow: 0 4px 20px rgba(27, 86, 231, 0.7);
}
.social {
  background-image: linear-gradient(225deg, #e176ae, #fb8310, #a45538);
  box-shadow: 0 4px 20px rgba(227, 131, 81, 0.7);
}
.my-art {
  background-image: linear-gradient(225deg, #5d70de, #901fa1, #381f4a);
  box-shadow: 0 4px 20px rgba(202, 62, 230, 0.7);
}
.js-active {
  background: none;
  color: #333333;
}
.js-active h2 {
  padding: 5px 15px;
  border-radius: 16px;
}
.js-active .about-me__title {
  background-image: linear-gradient(225deg, #926fc8, #0396b2, #235761);
  box-shadow: 0 4px 20px rgba(27, 86, 231, 0.7);
}
.js-active .social__title {
  background-image: linear-gradient(225deg, #e176ae, #fb8310, #a45538);
  box-shadow: 0 4px 20px rgba(227, 131, 81, 0.7);
}
.js-active .my-art__title {
  background-image: linear-gradient(225deg, #5d70de, #901fa1, #381f4a);
  box-shadow: 0 4px 20px rgba(202, 62, 230, 0.7);
}
.social__list {
  display: flex;
  flex-wrap: wrap;
  align-self: center;
  justify-content: center;
  margin: 10px;
  padding: 0;
  list-style: none;
}
.social__item {
  position: relative;
  margin: 0 10px 10px 10px;
  border-radius: 50%;
  box-shadow: 0 4px 15px rgba(23, 25, 86, 0.65);
}
.social__item--linkedin {
  background: linear-gradient(45deg, #003a92, #006cbd);
}
.social__item--git {
  background: linear-gradient(45deg, #333333, #626b73);
}
.social__item--instagram {
  background: linear-gradient(45deg, #405de6, #5851db, #833ab4, #c13584, #e1306c, #fd1d1d);
}
.social__item--vk {
  background: linear-gradient(45deg, #3b5998, #0078d7);
}
.social__link {
  display: block;
  width: 50px;
  height: 50px;
  border-radius: 50%;
}
.social__svg {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translateX(-50%) translateY(-50%);
}
.toggle__list {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  margin: 10px 0 0 0;
  padding: 0;
  list-style: none;
}
.toggle__item {
  position: relative;
  width: 250px;
  height: 250px;
  margin: 10px;
  background-color: #3644463d;
  border-radius: 15px;
  overflow: hidden;
  box-shadow: 0 6px 12px rgba(88, 41, 5, 0.7);
}
.toggle__preview {
  display: block;
  object-fit: cover;
}
.toggle__text {
  position: absolute;
  top: 5px;
  left: 5px;
  margin: 0;
  padding: 5px;
  text-align: center;
  font-size: 16px;
  border-radius: 12px;
  background: linear-gradient(50deg, #00649ae6, #00bcffe8);
}
